import React, { useState } from 'react'
import "./navbar.css"
import Logo from "../../assets/100h.png"
import Shoe from "../../assets/sneakers.png"
import Candle from "../../assets/candle.png"
import CentroTavola from "../../assets/centrotavola.png"
import Camera from "../../assets/camera.png"
import GiftBox from "../../assets/giftbox.png"
import { Link } from 'react-router-dom'
import ReorderIcon from '@material-ui/icons/Reorder';




const NavBar = () => {

    const [openMenu, setOpenMenu] = useState(false)

    const toggleMenu = () => {
        setOpenMenu(!openMenu)
    }

    return (
        <nav>
            <div className="left-side" id={openMenu ? "open" : "close"}>
                <Link to="/"><img className="logo-nav" src={Logo} alt="logo" /></Link>
                <div className="hidden-links" >
                    <Link to="/shoes"><img className="nav-icon shoe" src={Shoe} alt="shoe" /></Link>
                    <Link to="/candle"><img className="nav-icon" src={Candle} alt="candle" /></Link>
                    <Link to="/candle"><img className="nav-icon" src={CentroTavola} alt="candle" /></Link>
                    <Link to="/candle"><img className="nav-icon" src={Camera} alt="candle" /></Link>
                    <Link to="/candle"><img className="nav-icon giftbox" src={GiftBox} alt="candle" /></Link>
                </div>
            </div>
            <div className="right-side">
                <Link to="/shoes"><img className="nav-icon shoe" src={Shoe} alt="shoe" /></Link>
                <Link to="/candle"><img className="nav-icon" src={Candle} alt="candle" /></Link>
                <Link to="/candle"><img className="nav-icon" src={CentroTavola} alt="candle" /></Link>
                <Link to="/candle"><img className="nav-icon" src={Camera} alt="candle" /></Link>
                <Link to="/candle"><img className="nav-icon giftbox" src={GiftBox} alt="candle" /></Link>
            </div>
            <button onClick={toggleMenu} aria-label="menu">
                <ReorderIcon className="btn" />
            </button>
        </nav>
    )
}

export default NavBar