import React from 'react'
import Carousel from '../components/carousel/Carousel'
import "../styles/home.css"
import Section from "../components/home-page-section/Section"
import Shoes from "../assets/sezione-scarpe.jpg"
import Candles from "../assets/sezione-candele.jpg"
import Christmas from "../assets/speciale-natale.jpg"
import PhotoBook from "../assets/photo-book.jpg"
import Ideas from "../assets/spazio-idee.jpg"



function Home() {
    return (
        <div>
            <Carousel />
            <iframe  width="560" height="315" src="https://www.youtube.com/embed/4Jat4wBBl-c" title="YouTube video player" frameBorder="0" allowFullScreen></iframe>
            <div className="section-container">
                <Section 
                    imgUrl={Shoes}
                    title="Scarpe"
                />
                <Section 
                    imgUrl={Candles}
                    title="Candele"
                />
                <Section 
                    imgUrl={Christmas}
                    title="Speciale Natale"
                />
                <Section 
                    imgUrl={PhotoBook}
                    title="Servizio Fotografico"
                />
                <Section 
                    imgUrl={Ideas}
                    title="Spazio Idea"
                />
            </div>
        </div>
    )
}

export default Home
